body .contentAddres {
  display: flex;
}
body .contentAddres iframe {
  width: 60%;
  height: 600px;
}
body .contentAddres__contactForm {
  width: 40%;
  display: flex;
  flex-wrap: wrap;
  background-color: #176975;
}
body .contentAddres__contactForm .comentary {
  margin: 45px 0 0 45px;
}
body .contentAddres__contactForm .comentary form {
  width: 100%;
  position: relative;
}
body .contentAddres__contactForm .comentary form input {
  padding-left: 15px;
  font: 1.125em/45px "PTSans-Regular";
  color: #828282;
  border: 1px solid #176975;
  border-radius: 10px;
  outline: none;
}
body .contentAddres__contactForm .comentary form input[type*="text"],
body .contentAddres__contactForm .comentary form input[type*="tel"] {
  margin: 0 5px 10px;
  width: 245px;
  height: 45px;
}
body .contentAddres__contactForm .comentary form textarea {
  margin: 0 5px 10px;
  padding-left: 15px;
  width: 515px;
  height: 100px;
  font: 1.125em/45px "PTSans-Regular";
  color: #828282;
  border: 1px solid #176975;
  border-radius: 10px;
  outline: none;
  resize: none;
}
body .contentAddres__contactForm .comentary form button {
  margin: 20px 0 0 5px;
  width: 165px;
  height: 45px;
  font: 1.125em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 10px;
  outline: none;
}
body .contentAddres__contactForm .comentary form button:hover {
  background-color: #9e79ac;
  transition: .5s;
  cursor: pointer;
}
body .contentAddres__contactForm .comentary form .error {
  background-color: #ffd6d6;
}
body .contentAddres__contactForm .contacts {
  margin: 0 0 0 45px;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
body .contentAddres__contactForm .contacts p {
  font: 1.125em/1.25em "PTSans-Regular";
  color: #fff;
}
body .contentAddres__contactForm .contacts p a {
  color: #fff;
  text-decoration: none;
}
body .contentAddres__contactForm .contacts p a:hover {
  text-decoration: underline;
}
body .contentAddres__contactForm .social {
  margin: 0 0 0 45px;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
body .contentAddres__contactForm .social ul {
  display: flex;
  justify-content: space-between;
}
body .contentAddres__contactForm .social ul a {
  margin: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  color: #176975;
  text-decoration: none;
  border-radius: 50px;
}
body .contentAddres__contactForm .social ul a i {
  font-size: 2em;
  color: #fff;
}
body .contentAddres__contactForm .social ul a:hover {
  color: #fff;
  background-color: #9e79ac;
  transition: .5s;
}
body .contentAddres__contactForm .social p {
  color: #fff;
  text-align: right;
  font: 1em/1.125em "PTSans-Bold";
}
body .contentAddres__contactForm .social p span:nth-child(1) {
  font: 0.875em/1em "PTSans-Regular";
}
body #thanksComentary {
  margin: 0 auto;
  padding: 15px 0;
  width: 380px;
  min-height: 100px;
  position: relative;
  text-align: center;
  background: #fff;
  border-radius: 5px;
}
body #thanksComentary p {
  margin: 25px 0;
  font: 1.125em/1.25em "PTSans-Bold";
  color: #00929e;
}
body #thanksComentary span {
  display: block;
  font: 1em/1.125em "PTSans-Regular";
  color: #474747;
}
body #thanksComentary button {
  margin: 40px 0;
  width: 195px;
  height: 40px;
  font: 0.875em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00929e;
  border: none;
  border-radius: 5px;
  outline: none;
}
body #thanksComentary button:hover {
  cursor: pointer;
}
